.request {
	margin-top : 20px;

	@media screen and (min-width : $break-table) {
		margin-top : -80px;
	}
	@media screen and (min-width : $break-desktop) {
		margin-top : -120px;
	}
}

.request__container {
	margin    : 0 auto;
	max-width : 1492px;
	padding   : 0 14px;
}

.request__wrapper {
	background-color : $primary;
	border-radius    : 8px;
	margin           : 0 -14px;
	padding          : 32px 14px 32px 14px;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 50px 85px 32px 85px;
	}
}

.request__top {
	margin-bottom : 32px;

	@media screen and (min-width : $break-table) {
		align-items : end;
		display     : flex;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 34px;
	}
}

.request__title {
	color          : $dark;
	font-size      : 20px;
	font-weight    : 400;
	line-height    : 26px;
	margin-bottom  : 16px;
	text-transform : capitalize;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 28px;
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 32px;
		line-height : 32px;
	}
}

.request__text {
	color       : $dark;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;


	@media screen and (min-width : $break-table) {
		margin-left : 32px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.form--request {
	align-items           : center;
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(5, 1fr);
	position              : relative;
	z-index               : 0;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(6, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(5, 1fr);
	}

	.form__row {
		grid-column : 5 span;

		@media screen and (min-width : $break-table) {
			grid-column : 3 span;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 1 span;
		}
	}

	.b-btn--black {
		grid-column : 5 span;
		max-width   : 89px;
		padding     : 12px 32px;

		@media screen and (min-width : $break-table) {
			grid-column : 3 span;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 1 span;
			max-width   : 176px;
			padding     : 16px 32px;
		}
	}
}

