.pagination__link:hover {
  background-color: #D6D8C1;
}

.pagination__arrow {
  display: flex;
  height: 32px;
  width: 32px;
}
@media screen and (min-width: 1280px) {
  .pagination__arrow {
    height: 48px;
    width: 48px;
  }
}
.pagination__arrow svg circle {
  fill: transparent;
  transition: fill 0.3s ease;
}
.pagination__arrow svg path {
  position: relative;
  stroke: #1C1C1C;
  z-index: 2;
}
.pagination__arrow:hover svg {
  object-fit: cover;
}
.pagination__arrow:hover svg circle {
  fill: #D6D8C1;
  transition: fill 0.3s ease;
}
.pagination__arrow:hover svg path {
  stroke: #1C1C1C;
}

.pagination__item--first {
  margin-right: 16px;
}
@media screen and (min-width: 1280px) {
  .pagination__item--first {
    margin-right: 32px;
  }
}

.pagination__item--last {
  margin-left: 16px;
}
@media screen and (min-width: 1280px) {
  .pagination__item--last {
    margin-left: 32px;
  }
}