.philosophy {
	padding : 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0 0 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0 0 0;
	}
}

.philosophy__wrapper {
	display               : grid;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.philosophy__left {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.philosophy__title {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 19px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}
}

.philosophy__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
		max-width   : 350px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 8px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 16px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 24px;
			}
		}
	}
}

.philosophy__subtitle {
	font-size   : 16px;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 26px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 32px;
		max-width   : 300px;
	}
}

.philosophy__image {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column  : 6/13;
		padding-left : 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	img {
		object-fit : cover;
		width      : 100%;
	}
}


