.footer {
	background-color : #E9EADD;
	padding-top      : 40px;

	@media screen and (min-width : $break-table) {
		padding-top : 60px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-top : 80px;
	}
}

.footer__logo {
	svg {
		path {
			fill : $dark;
		}
	}
}

.footer__top {

	border-bottom  : 1px solid #D9D9D9;
	padding-bottom : 35px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 43px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
		padding-bottom  : 52px;
	}
}

.contacts--footer {
	display        : flex;
	flex-direction : column;
	margin-top     : 62px;

	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		justify-content : space-between;
		margin-top      : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 0;
	}

	.contacts__item {
		margin-bottom : 0;

		&:not(:first-child) {

			@media screen and (min-width : $break-table) {
				margin-left : 40px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-left : 80px;
			}
		}

		&:not(:last-child) {
			margin-bottom : 24px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 0;
			}
		}
	}
}

.nav--footer {
	border-bottom : none;
	padding       : 0;
	@media screen and (min-width : $break-table) {
		border-bottom : 1px solid #D9D9D9;
		padding       : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 50px 0;
	}

	.nav__list {
		display        : flex;
		flex-direction : column;

		@media screen and (min-width : $break-table) {
			flex-direction  : row;
			justify-content : center;
		}
	}

	.nav__item {

		&:not(:first-child) {
			margin-left : 0;
		}

		&:not(:last-child) {
			margin-bottom : 16px;
		}

		@media screen and (min-width : $break-table) {

			&:not(:first-child) {
				@media screen and (min-width : $break-table) {
					margin-left : 94px;
				}
			}

			&:not(:last-child) {
				@media screen and (min-width : $break-table) {
					margin-bottom : 0;
				}
			}
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.nav__link {
		color : $dark;
	}
}

.footer__bottom {
	display         : flex;
	justify-content : space-between;
	padding         : 32px 0 40px 0;

	@media screen and (min-width : $break-table) {
		flex-direction : column;
		padding        : 0;
	}
}

.footer__wrap {
	align-items    : flex-start;
	display        : flex;
	flex-direction : column;
	width          : 50%;

	@media screen and (min-width : $break-table) {
		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
		padding         : 32px 0;
		width           : 100%;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 48px 0;
	}
}

.footer__link {
	color       : #818181;
	font-size   : 12px;
	font-weight : 500;
	line-height : 20px;

	&:not(:last-child) {
		margin-bottom : 16px;
		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
		}
	}

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		order         : 0;
	}
}

.footer__address {
	color         : #818181;
	font-size     : 12px;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 16px;
	order         : -1;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		order         : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
