.partners {
	padding : 40px 0 20px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 32px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 128px 0 80px 0;
	}
}

.partners__title {
	margin-bottom : 25px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}

.partners__grid {
	align-items           : center;
	display               : grid;
	grid-template-columns : repeat(12, 1fr);

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}

.partners__item {
	border      : 1px solid #BFBCB4;
	grid-column : 6 span;
	padding     : 0;
	text-align  : center;

	&:nth-child(3n-1) {
		border-left : none;
	}

	&:nth-child(4n-1) {
		border-top : none;
	}

	&:nth-child(5n-1) {
		border-left : none;
		border-top  : none;
	}

	@media screen and (min-width : $break-table) {
		grid-column : 3 span;
		&:nth-child(4n-1) {
			border-top : 1px solid #BFBCB4;
		}

		&:nth-child(5n-1) {
			border-top : 1px solid #BFBCB4;
		}
		&:not(:first-child) {
			border-left : none;
		}
	}

	@media screen and (min-width : $break-desktop) {
		padding : 36px 0;

	}

}
