.projects-list {
	padding : 80px 0;
}

.projects-list__wrapper {
	margin : -24px 0;

	@media screen and (min-width : $break-desktop) {
		margin : 0;
	}
}

.projects-group {
	display         : flex;
	flex-wrap       : wrap;
	justify-content : space-between;
	position        : relative;

	&:not(:last-child) {
		border-bottom : 1px solid #BFBCB4;
	}

	@media screen and (min-width : $break-table) {
		border-bottom : none;
		border-top    : 1px solid #BFBCB4;
		padding       : 24px 0;

		&:last-child {
			border-bottom : 1px solid #BFBCB4;
		}

		&:not(:last-child) {
			border-bottom : none;
		}

		&:before {
			background-color : #BFBCB4;
			content          : "";
			height           : calc(100% - 48px);
			left             : 50%;
			position         : absolute;
			top              : 50%;
			transform        : translate(-50%, -50%);
			width            : 1px;
		}
	}

	@media screen and (min-width : $break-desktop) {
		padding : 24px 0;
	}
}

.project-item {
	padding  : 32px 0;
	position : relative;
	width    : 100%;

	&:not(:last-child) {
		&:before {
			background-color : #BFBCB4;
			bottom           : 0;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			width            : 100%;
		}
	}

	@media screen and (min-width : $break-table) {
		padding : 0;
		width   : 50%;

		&:before {
			display : none;
		}
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:first-child {


		@media screen and (min-width : $break-table) {
			padding-right : 12px;
		}

		@media screen and (min-width : $break-desktop) {
			padding-right : 24px;
		}

	}

	&:last-child {


		@media screen and (min-width : $break-table) {
			padding-left : 12px;
		}

		@media screen and (min-width : $break-desktop) {
			padding-left : 24px;
		}
	}
}

.project-item__img {
	margin-bottom : 27px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
	}

	img {
		object-fit : cover;
		width      : 100%;
	}
}

.project-item__info {
	position : relative;
}

.project-item__title {
	color          : $dark;
	font-family    : 'Bigilla';
	font-size      : 28px;
	font-style     : normal;
	font-weight    : 700;
	line-height    : 34px;
	margin-bottom  : 16px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 48px;
		line-height   : 56px;
		margin-bottom : 24px;
	}
}

.project-item__wrap {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.project-about {
	align-items : center;
	display     : flex;
}

.project-about__item {
	display : flex;

	&:not(:first-child) {
		margin-left : 32px;
	}
}

.project-about__wrap {
	margin-left : 12px;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 24px;

	}
}

.project-about__name {
	color          : rgba(28, 28, 28, 0.6);
	font-size      : 14px;
	font-weight    : 400;
	line-height    : 20px;
	text-transform : capitalize;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.project-about__meaning {
	color          : $dark;
	font-size      : 16px;
	font-weight    : 400;
	line-height    : 20px;
	text-transform : capitalize;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		font-weight : 400;
		line-height : 24px;
	}
}

.project-about__icon {
	height : 40px;
	width  : 40px;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 48px;
		width  : 48px;
	}
}

.project-item__link {
	align-items     : center;
	border          : 1px solid #BFBCB4;
	display         : flex;
	height          : 30px;
	justify-content : center;
	position        : absolute;
	right           : 0;
	top             : 0;
	width           : 30px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height   : 48px;
		position : unset;
		width    : 48px;
	}
}
