.builders {
	padding : 20px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 80px 0;
	}
}

.builders__wrapper {
	padding-bottom : 80px;
	position       : relative;

	@media screen and (min-width : $break-table) {
		padding-bottom : 0;
	}
}

.builders__top {
	align-items     : flex-end;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 24px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}

	.b-btn--outline-gray {
		bottom    : 0;
		left      : 50%;
		padding   : 12px 24px;
		position  : absolute;
		transform : translateX(-50%);

		@media screen and (min-width : $break-table) {
			left      : 0;
			position  : unset;
			transform : unset;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 16px 32px;
		}
	}
}

.builders__gird {
	margin : 0 -14px;

	@media screen and (min-width : $break-table) {
		margin : 0 -48px;
	}

	@media screen and (min-width : 1038px) {
		margin : 0;
	}
}

.builder-item {

	grid-column : 4 span;
	padding     : 20px 20px;
	position    : relative;

	@media screen and (min-width : $break-table) {
		padding : 24px 24px;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 44px 35px;
	}

	&:before {
		color             : rgba(0, 0, 0, 0.27);
		content           : "0" counter(count);
		counter-increment : count;
		font-size         : 24px;
		font-weight       : 400;
		line-height       : 32px;
		position          : absolute;
		right             : 24px;
		top               : 20px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			right : 24px;
			top   : 24px;
		}
		@media screen and (min-width : $break-desktop) {
			right : 60px;
			top   : 44px;
		}
	}
}

.builder-item__icon {
	margin-bottom : 25px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 48px;
	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 25px;
	}
}

.builder-item__title {
	color         : $dark;
	font-size     : 24px;
	font-weight   : 400;
	line-height   : 32px;
	margin-bottom : 25px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}

.builder-item__text {
	color       : $dark;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;
}

.swiper--builder {


	@media screen and (min-width : 1038px) {
		border : 1px solid #BFBCB4;
	}

	.swiper-wrapper {

		counter-reset : count;
		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : 1038px) {
			display               : grid;
			grid-template-columns : repeat(12, 1fr);
		}
	}

	.swiper-slide {
		border-bottom : 1px solid #BFBCB4;
		border-top    : 1px solid #BFBCB4;
		display       : flex;
		height        : auto;
		width         : 65%;

		&:first-child {
			border-left : 1px solid #BFBCB4;

			margin-left : 14px;

			@media screen and (min-width : $break-table) {
				margin-left : 48px;
			}

			@media screen and (min-width : 1038px) {
				margin : 0;
			}
		}

		&:not(:first-child) {
			border-left : 1px solid #BFBCB4;
		}

		&:last-child {
			border-right : 1px solid #BFBCB4;
			margin-right : 14px;

			@media screen and (min-width : $break-table) {
				margin-right : 48px;
			}

			@media screen and (min-width : 1038px) {
				margin : 0;
			}
		}

		@media screen and (min-width : $break-table) {
			width : 44%;
		}

		@media screen and (min-width : 1038px) {
			border-bottom : none;
			border-top    : none;
			grid-column   : 4 span;
			width         : 100%;

			&:not(:first-child) {
				border-left : 1px solid #BFBCB4;
			}

			&:nth-child(4) {
				border-left : none;
			}

			&:nth-child(n+4):nth-child(-n+6) {
				border-top : 1px solid #BFBCB4;
			}

			&:first-child {
				border-left : none;
			}

			&:last-child {
				border-right : none;
			}
		}

	}
}
