// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 400;
$btn-line-height           : 1.25;
$btn-border-width          : 2px;
$btn-padding               : 16px 32px;
$btn-font-size             : 16px;
$btn-border-radius         : 4px;
$btn-transition            : background .3s ease, color .3s ease, border .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 14px 30px;
$btn-font-size-big         : 16px;
// Small
$btn-padding-small         : 8px 30px;
$btn-font-size-small       : 13px;

// COLOR
// White
$btn-white-bg              : transparent;
$btn-white-color           : #FFF;
$btn-white-bg-hover        : #D6D8C1;
$btn-white-color-hover     : #0B081B;
$btn-black-color-outline   : rgba(255, 255, 255, 0.24);

// Black

// Black

$btn-black-bg              : #1C1C1C;
$btn-black-color           : #FFF;
$btn-black-color-outline   : transparent;
$btn-black-bg-hover        : #D6D8C1;
$btn-black-color-hover     : #1C1C1C;


// Gray
$btn-gray-bg               : transparent;
$btn-gray-color            : #1C1C1C;
$btn-gray-bg-hover         : #D6D8C1;
$btn-gray-color-hover      : #1C1C1C;
$btn-gray-color-outline    : #BFBCB4;


// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	align-items        : center;
	border             : $btn-border-width solid transparent;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	display            : inline-flex;
	flex-flow          : row nowrap;
	font-size          : $btn-font-size;
	font-weight        : $btn-font-weight;
	justify-content    : center;
	line-height        : $btn-line-height;
	padding            : $btn-padding;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	text-transform     : uppercase;
	transition         : $btn-transition;
	user-select        : none;
	vertical-align     : middle;
	white-space        : nowrap;

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		background : inherit;
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR

	// Black
	&--black {
		background-color : $btn-black-bg;
		color            : $btn-black-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-black-bg-hover;
			color            : $btn-black-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-black-bg;
				color            : $btn-black-color;
			}
		}
	}


	// OUTLINE


	// Black
	&--outline-black {
		background-color : transparent;
		border           : 1px solid $btn-black-bg;
		color            : $btn-black-color-outline;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-black-bg-hover;
			color            : $btn-black-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-black-bg;
				color            : $btn-black-color-outline;
			}
		}
	}

	// White
	&--outline-white {
		background-color : transparent;
		border           : 2px solid rgba(255, 255, 255, 0.24);
		color            : $btn-white-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-white-bg-hover;
			border           : 2px solid transparent;
			color            : $btn-white-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-white-bg;
			}
		}
	}

	// Black
	&--outline-gray {
		background-color : transparent;
		border           : 2px solid $btn-gray-color-outline;
		color            : $btn-gray-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-gray-bg-hover;
			border           : 2px solid transparent;
			color            : $btn-gray-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-gray-bg;
				color            : $btn-gray-color-outline;
			}
		}
	}


	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes loadingSpin {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
