.article {
	padding : 104px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 104px 0 64px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 176px 0 160px 0;
	}
}

.article__wrapper {
	display               : grid;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-row-gap          : 32px;
		grid-template-columns : repeat(12, 1fr);
	}
	@media screen and (min-width : $break-desktop) {
		grid-row-gap : 64px;
	}
}

.article__item {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 3/11;
	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 4/10;
	}
}

.article__poster {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/13;
	}
	@media screen and (min-width : $break-desktop) {

	}

	img {
		border-radius : 8px;
	}
}

.article__title {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.article__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 24px;
		}
	}
}

.article__head {
	align-items           : flex-start;
	display               : grid;
	grid-row-gap          : 48px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 24px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}
	@media screen and (min-width : $break-desktop) {
		grid-row-gap  : 0;
		margin-bottom : 32px;
	}

	.b-btn--outline-gray {
		grid-column : 4 span;
		grid-row    : 1/1;
		max-width   : 159px;
		padding     : 12px 24px;

		@media screen and (min-width : $break-table) {
			grid-column : 3/6;
		}
		@media screen and (min-width : $break-desktop) {
			grid-column : 1/3;
			grid-row    : 1/1;
			max-width   : 175px;
			padding     : 16px 32px;
		}
	}
}


.article__date {
	background-color : #D6D8C1;
	border-radius    : 8px;
	color            : $dark;
	font-size        : 14px;
	font-weight      : 400;
	grid-column      : 1/3;
	line-height      : 20px;
	max-width        : 90px;
	padding          : 8px 10px;
	text-align       : center;
	text-transform   : capitalize;

	@media screen and (min-width : $break-table) {
		grid-column : 3/5;
	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		grid-column : 4/6;
		line-height : 30px;
		max-width   : 127px;
		padding     : 10px 22px;
	}
}

.article__wrap {
	grid-column : 11/13;

	@media screen and (min-width : $break-table) {
		grid-column : 8/10;
	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 11/13;
	}
}

.socials {
	display         : flex;
	justify-content : flex-end;

	&__item {
		& + & {
			margin-left : 8px;
		}
	}

	&__link {
		display : block;
		height  : 36px;
		width   : 36px;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			height : 48px;
			width  : 48px;
		}
	}
}
