.tabs {
	bottom   : 20px;
	left     : 0;
	position : absolute;
	width    : 100%;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		bottom : 108px;
	}
}

.tabs-content {
	background-size : cover;
}

.tabs__list {
	display         : flex;
	justify-content : center;

	@media screen and (min-width : $break-table) {
		justify-content : start;
		padding-left    : max(44px, calc((100% - 1350px) / 2));
	}
}

.tabs__item {
	& + & {
		margin-left : 11px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 24px;
		}
	}
}

.tabs__btn {
	align-items      : center;
	background-color : transparent;
	border           : none;
	border-radius    : 100%;
	display          : flex;
	height           : 52px;
	justify-content  : center;
	overflow         : hidden;
	padding          : 0;
	position         : relative;
	width            : 52px;

	&:before {
		background : rgba(242, 239, 228, 0.5);
		bottom     : 0;
		content    : "";
		height     : 100%;
		left       : 0;
		opacity    : 0;
		position   : absolute;
		right      : 0;
		top        : 0;
		visibility : hidden;
		width      : 100%;
	}

	@media screen and (min-width : $break-table) {
		height : 60px;
		width  : 60px;
	}

	@media screen and (min-width : $break-desktop) {
		height : 80px;
		width  : 80px;
	}

	&.active {
		border : 3px solid #F2EFE4;

		&:before {

			opacity    : 1;
			visibility : visible;
		}

	}

	img {
		object-fit     : cover;
		pointer-events : none;
		width          : 100%;
	}
}

.tabs__btn.active {
	background-color : #CCC;
}

.tabs-content {
	color      : $white;
	display    : none;
	min-height : 380px;
	padding    : 0;

	@media screen and (min-width : $break-table) {
		min-height : 361px;
		padding    : 40px 0 40px 0;
	}

	@media screen and (min-width : $break-desktop) {
		min-height : 933px;
		padding    : 160px 0 108px 0;
	}
}

.tabs-content.active {
	display : block;
}

.completed {
	padding-bottom : 74px;
	padding-top    : 218px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 0;
		padding-top    : 0;
	}
}

.completed__wrapper {
	position : relative;

	.b-btn--outline-white {
		border    : 2px solid #BFBCB4;
		bottom    : -61px;
		color     : $dark;
		left      : 50%;
		padding   : 12px 24px;
		position  : absolute;
		transform : translateX(-50%);
		z-index   : 2;

		@media screen and (min-width : $break-table) {
			border       : 2px solid #BFBCB4;
			bottom       : 40px;
			color        : $white;
			left         : unset;
			margin-right : max(44px, calc((100% - 1320px) / 2));
			padding      : 14px 32px;
			right        : 0;
			transform    : unset;

			&:hover {
				color : $dark;
			}
		}

		@media screen and (min-width : $break-desktop) {
			bottom : 82px;

		}
	}
}

.completed__wrap {
	display        : flex;
	flex-direction : column;
	left           : 0;
	position       : absolute;
	top            : -211px;
	width          : 100%;

	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		justify-content : space-between;
		position        : unset;
		top             : unset;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.completed__title {
	color          : $dark;
	font-family    : 'Bigilla';
	font-size      : 36px;
	font-style     : normal;
	font-weight    : 700;
	line-height    : 42px;
	margin-bottom  : 24px;
	max-width      : 250px;
	padding-left   : 14px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		color         : $white;
		margin-bottom : 0;
		padding-left  : 0;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		line-height : 64px;
		max-width   : 400px;
	}
}

.completed__about {
	background-color : #F2EFE4;
	border-radius    : 8px;
	padding          : 0 14px;

	@media screen and (min-width : $break-table) {
		min-width : 189px;
		padding   : 24px 24px;
	}

	@media screen and (min-width : $break-desktop) {
		min-width : 343px;
		padding   : 48px 48px;
	}
}

.completed__subtitle {
	color         : #1C1C1C;
	font-size     : 20px;
	font-weight   : 400;
	line-height   : 26px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 28px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 32px;
		line-height   : 32px;
		margin-bottom : 32px;
	}

}

.complete-list {
	display : flex;
	width   : 100%;

	@media screen and (min-width : $break-table) {
		flex-direction : column;
	}
}

.completed-list__item {
	align-items : center;
	display     : flex;


	&:not(:last-child) {

		@media screen and (min-width : $break-table) {
			margin-bottom : 24px;
		}
	}

	&:not(:first-child) {
		margin-left : 24px;
		@media screen and (min-width : $break-table) {
			margin-left : 0;
		}

	}
}

.completed-list__wrap {
	margin-left : 8px;

	@media screen and (min-width : $break-table) {
		margin-left : 12px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 24px;
	}
}

.completed-list__icon {
	flex-shrink : 0;
	height      : 40px;
	width       : 40px;
	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 48px;
		width  : 48px;
	}
}

.completed-list__name {
	color       : rgba(28, 28, 28, 0.6);
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.completed-list__about {
	color       : $dark;
	font-size   : 16px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 24px;
	}
}
