.process {
	padding : 40px 0 20px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 64px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 160px 0 160px 0;
	}
}

.process__head {
	align-items     : flex-start;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 24px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}

.process__wrap {
	display : none;
	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		align-items : flex-start;
		display     : flex;

	}
}

.process__text {
	color        : $dark;
	font-size    : 16px;
	font-weight  : 400;
	line-height  : 24px;
	margin-right : 48px;
	max-width    : 291px;

}

.process__grid {
	display               : grid;
	grid-column-gap       : 27px;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(6, 1fr);
	padding               : 0 10px;

	@media screen and (min-width : $break-table) {
		grid-gap              : 43px;
		grid-template-columns : repeat(12, 1fr);
		padding               : 0;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 69px;
	}
}

.process-item {
	align-items     : center;
	border          : 2px solid #BFBCB4;
	border-radius   : 8px;
	cursor          : pointer;
	display         : flex;
	flex-direction  : column;
	grid-column     : 3 span;
	height          : 123px;
	justify-content : center;
	position        : relative;

	@media screen and (min-width : $break-table) {
		grid-column : 3 span;
		height      : 136px;
	}
	@media screen and (min-width : $break-desktop) {
		height : 278px;
	}


	&:before {
		background-image : url("../images/icon/button-arrow-top.svg");
		background-size  : cover;
		content          : "";
		height           : 24px;
		position         : absolute;
		right            : 12px;
		top              : 12px;
		width            : 24px;

		@media screen and (min-width : $break-table) {
			height : 32px;
			width  : 32px;
		}
		@media screen and (min-width : $break-desktop) {
			height : 42px;
			right  : 20px;
			top    : 20px;
			width  : 42px;
		}
	}
}

.process-item__name {
	color          : $dark;
	font-size      : 14px;
	font-weight    : 400;
	line-height    : 20px;
	margin-top     : 50px;
	text-transform : capitalize;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 32px;
		margin-top  : 85px;
	}
}

.process-item__count {
	background-color : $primary;
	/* height: 107px; */
	left             : 0;
	margin-left      : -22px;
	padding          : 12px 0px;
	position         : absolute;
	top              : 50%;
	transform        : translateY(-50%);
	/* width: auto; */
	z-index          : 2;

	img {
		height     : 50px;
		max-width  : none;
		object-fit : cover;
		width      : 37px;

		@media screen and (min-width : $break-table) {
			height : 72px;
			width  : 46px;
		}
		@media screen and (min-width : $break-desktop) {
			height : 139px;
			width  : 91px;

		}
	}

	@media screen and (min-width : $break-table) {
		margin-left : -28px;
		padding     : 7px 0;
	}
	@media screen and (min-width : $break-desktop) {
		margin-left : -58px;
		padding     : 6px 0;
	}
}
