.help {
	background-color : #E9EADD;
	padding          : 40px 0 40px 0;
	position         : relative;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 144px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 160px 0 160px 0;
	}

}

.help__wrapper {


	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		display         : flex;
		justify-content : space-between;

	}

	.b-btn--outline-gray {
		padding : 12px 24px;

		@media screen and (min-width : $break-table) {
			bottom    : 64px;
			position  : absolute;
			right     : 50%;
			transform : translateX(100%);
		}

		@media screen and (min-width : $break-desktop) {
			padding   : 16px 32px;
			position  : unset;
			transform : unset;
		}
	}
}

.help__left {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		display               : grid;
		grid-template-columns : repeat(6, 1fr);

	}
	@media screen and (min-width : $break-desktop) {
		align-items    : flex-start;
		display        : flex;
		flex-direction : column;
		max-width      : 650px;
	}
}

.help__title {
	grid-column   : 3 span;
	margin-bottom : 16px;
	@media screen and (min-width : $break-table) {
		font-size     : 36px;
		line-height   : 42px;
		margin-bottom : 0;
		max-width     : 258px;

	}
	@media screen and (min-width : $break-desktop) {
		font-size     : 64px;
		line-height   : 64px;
		margin-bottom : 24px;
		max-width     : none;
	}
}

.help__text {
	color         : $dark;
	font-size     : 14px;
	font-weight   : 400;
	grid-column   : 3 span;
	line-height   : 20px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		max-width     : 336px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 32px;
		max-width     : 312px;
	}
}

.help__right {

	width : 100%;
}

.help-item {
	display               : grid;
	grid-template-columns : repeat(6, 1fr);
	padding               : 0 16px;
	position              : relative;
	width                 : 100%;

	@media screen and (min-width : $break-table) {
		border-bottom : 1px solid #BFBCB4;
		padding       : 32px 0;
	}
	@media screen and (min-width : $break-desktop) {
		display        : flex;
		padding-bottom : 48px;
		padding-top    : 48px;
	}

	&:before {
		background-image : url("../images/icon/button-arrow-top.svg");
		background-size  : cover;
		content          : "";
		height           : 32px;
		position         : absolute;
		right            : 0;
		top              : 0;
		width            : 32px;

		@media screen and (min-width : $break-table) {
			bottom : 32px;
			left   : 0;
			right  : unset;
			top    : unset;
		}
		@media screen and (min-width : $break-desktop) {
			bottom : unset;
			height : 42px;
			left   : unset;
			right  : 0;
			top    : 48px;
			width  : 42px;
		}
	}
}

.help-item__count {
	color         : rgba(0, 0, 0, 0.27);
	font-size     : 20px;
	font-weight   : 400;
	grid-column   : 6 span;
	line-height   : 26px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		grid-column   : 3 span;
		margin-bottom : 0;
	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 32px;
		line-height : 48px;
	}
}

.help-item__wrap {
	grid-column : 6 span;
	max-width   : 351px;

	@media screen and (min-width : $break-table) {
		grid-column : 3 span;
	}
	@media screen and (min-width : $break-desktop) {
		margin-left : 75px;
	}

}

.help-item__name {
	color         : #1C1C1C;
	font-size     : 20px;
	font-weight   : 400;
	line-height   : 26px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size     : 32px;
		line-height   : 48px;
		margin-bottom : 24px;
	}
}

.help-item__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}

	&:not(:last-child) {
		margin-bottom : 16px;
	}
}

.help-item__list {

	li {
		color          : $dark;
		font-size      : 14px;
		line-height    : 22px;
		padding-left   : 10px;
		position       : relative;
		text-transform : capitalize;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
		}

		&:before {
			background-color : $dark;
			border-radius    : 100%;
			content          : "";
			height           : 4px;
			left             : 0;
			position         : absolute;
			top              : 9px;
			width            : 4px;
		}

	}
}

.swiper--help {
	margin : 0 -16px;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}

	.swiper-slide {
		align-items    : flex-start;
		display        : flex;
		flex-direction : column;
		height         : auto;
		margin         : 0 10px;
		width          : 62%;

		&:not(:first-child) {
			border-left : 1px solid #BFBCB4;
		}

		&:last-child {
			margin-right : 14px;
		}

		@media screen and (min-width : $break-table) {
			border-left : none;
			margin      : 0;
			width       : 100%;

			&:not(:first-child) {
				border-left : none;
			}

			&:last-child {
				margin-right : 0;
			}
		}

		@media screen and (min-width : $break-desktop) {
			&:first-child {
				margin-top : -48px;
			}
		}


	}

	.swiper-wrapper {
		@media screen and (min-width : $break-table) {
			flex-direction : column;
		}
	}
}
