.learn {
	padding : 40px 0;
	@media screen and (min-width : $break-table) {
		padding : 32px 0 64px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 80px 0 160px 0;
	}
}

.learn__wrapper {


	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		display : flex;
	}
}

.learn__title {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		max-width     : 400px;
	}
}

.learn__item {
	display        : flex;
	flex-direction : column;
	min-height     : 280px;
	padding        : 32px 25px 32px 25px;
	position       : relative;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		padding : 32px 32px;
	}

	&:not(:first-child) {
		border-left : 1px solid #BFBCB4;
	}

	&:before {
		background-image : url("../images/icon/button-arrow-top.svg");
		background-size  : cover;
		content          : "";
		height           : 32px;
		position         : absolute;
		right            : 24px;
		top              : 24px;
		width            : 32px;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			height : 42px;
			right  : 32px;
			top    : 32px;
			width  : 42px;
		}
	}
}

.learn__icon {

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-top : 22px;
	}
}

.learn__text {
	color          : $dark;
	font-size      : 24px;
	font-weight    : 400;
	line-height    : 32px;
	margin-top     : auto;
	text-transform : capitalize;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		
	}
}


.swiper--learn {
	border-bottom : 1px solid #BFBCB4;
	border-top    : 1px solid #BFBCB4;
	margin        : 0;
	overflow      : visible;
	width         : 100%;
	@media screen and (min-width : $break-table) {
		overflow : hidden;
	}

	.swiper-wrapper {

	}

	.swiper-slide {
		border-left : 1px solid #BFBCB4;
		width       : 76%;

		&:last-child {
			border-right : 1px solid #BFBCB4;
		}

		@media screen and (min-width : $break-table) {
			width : 33%;
		}
		@media screen and (min-width : $break-desktop) {
			width : 33%;
		}


	}
}
