.team {
	padding : 80px 0 120px 0;

	@media screen and (min-width : $break-table) {
		padding : 40px 0 80px 0;
	}

	@media screen and (min-width : $break-desktop) {
	}
}

.team__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}

.team__wrapper {
	position : relative;
}

.tabs-content--contact {
	min-height : auto;
	padding    : 0;
}

.founder-item--contact {
	.b-btn--outline-gray {
		@media screen and (min-width : $break-table) {
			display : none;
		}

		@media screen and (min-width : $break-desktop) {
			display : flex;
		}
	}
}

.tabs--contact {
	bottom : -80px;
	left   : unset;
	right  : 0;
	width  : 100%;

	@media screen and (min-width : $break-table) {
		bottom : 0;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.tabs__list {
		justify-content : center;
		padding-left    : 0;
		@media screen and (min-width : $break-table) {
			justify-content : start;
		}
	}

	.tabs__item {
		&:not(:first-child) {
			margin-left : 16px;
		}
	}

	.tabs__btn {
		height : 52px;
		width  : 52px;

		@media screen and (min-width : $break-desktop) {
			height : 64px;
			width  : 64px;
		}

		&.active {
			border : 2px solid $white;
		}
	}
}
