.contact-list {
	padding : 32px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0 40px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0;
	}

}

.contact-list__grid {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contact-list__item {
	border          : 1px solid #BFBCB4;
	border-radius   : 8px;
	display         : flex;
	flex-direction  : column;
	grid-column     : 4 span;
	justify-content : space-between;
	min-height      : 130px;
	padding         : 16px 16px;

	@media screen and (min-width : $break-table) {
		min-height : 142px;
	}

	@media screen and (min-width : $break-desktop) {
		min-height : 240px;
		padding    : 32px 32px;
	}
}

.contact-list__icon {
	height      : 40px;
	margin-left : auto;
	width       : 40px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 48px;
		width  : 48px;
	}
}

.contact-list__name {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.contact-list__link {
	color       : $dark;
	font-size   : 18px;
	font-weight : 400;
	line-height : 26px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 32px;
	}

}
