.other {
	padding : 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0;
	}
}

.other__wrapper {
	padding-bottom : 56px;
	position       : relative;

	@media screen and (min-width : $break-table) {
		padding-bottom : 0;
	}
}

.other__head {
	align-items     : flex-start;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}

	.b-btn--outline-gray {
		bottom   : 0;
		left     : 0;
		padding  : 12px 26px;
		position : absolute;

		@media screen and (min-width : $break-table) {
			padding  : 14px 32px;
			position : unset;
		}
	}
}
