.error {
	display         : flex;
	flex-direction  : column;
	height          : 100vh;
	justify-content : center;
}

.error__wrapper {
	background-image    : url("../images/404/error-bg.jpg");
	background-position : center;
	background-repeat   : no-repeat;
	background-size     : 164px 267px;
	margin              : 0 auto;
	padding             : 90px 0;


	@media screen and (min-width : $break-table) {
		background-size : 164px 300px;
	}

	@media screen and (min-width : $break-desktop) {
		background-size : 380px 822px;
		min-width       : 800px;
		padding         : 190px 0;
	}

}

.error__wrap {
	position : relative;

	.b-btn--outline-gray {
		bottom    : -150px;
		left      : 50%;
		padding   : 12px 36px;
		position  : absolute;
		transform : translateX(-50%);

		@media screen and (min-width : $break-table) {
			background-size : 164px 300px;
			bottom          : unset;
			left            : unset;
			padding         : 12px 24px;
			right           : 0;
			top             : 0;
			transform       : unset;

		}

		@media screen and (min-width : $break-desktop) {
			padding : 16px 32px;
		}

	}
}

.error__text {
	left      : 50%;
	position  : absolute;
	top       : -119px;
	transform : translateX(-106%);

	@media screen and (min-width : $break-table) {
		bottom    : 0;
		left      : 32px;
		top       : unset;
		transform : unset;
	}

	@media screen and (min-width : $break-desktop) {
		left : 0;
	}

}

.error__title {
	background              : #FFF;
	background              : radial-gradient(circle farthest-corner at center center, #FFF 56%, #000 41%);
	-webkit-background-clip : text;
	color                   : #1C1C1C;
	font-family             : 'Bigilla';
	font-size               : 36px;
	font-style              : normal;
	font-weight             : 700;
	line-height             : 42px;
	-webkit-text-fill-color : transparent;
	text-transform          : uppercase;

	@media screen and (min-width : $break-table) {
		background              : #FFF;
		background              : radial-gradient(circle farthest-corner at center center, #FFF 39%, #000 41%);
		-webkit-background-clip : text;
		font-size               : 60px;
		line-height             : 60px;
		-webkit-text-fill-color : transparent;
	}

	@media screen and (min-width : $break-desktop) {
		background              : #FFF;
		background              : radial-gradient(circle farthest-corner at center center, #FFF 38%, #000 41%);
		-webkit-background-clip : text;
		font-size               : 128px;
		line-height             : 129px;
		-webkit-text-fill-color : transparent;
	}

	span {
		display : block;
	}
}

.title-top {
	@media screen and (min-width : $break-table) {
		margin-left : 27px
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 0;
	}
}

.title-bottom {

	margin-left : 77px;

	@media screen and (min-width : $break-table) {
		margin-left : 141px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 234px;
	}
}
