.project-main {
	padding : 24px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0;
	}

}

.project-main__head {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 24px;

	@media screen and (min-width : $break-table) {
		border-bottom  : 1px solid #BFBCB4;
		margin-bottom  : 32px;
		padding-bottom : 34px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 80px;
	}
}

.project-main__item {
	border        : 1px solid #BFBCB4;
	border-radius : 60px;
	color         : #1C1C1C;
	font-size     : 10px;
	font-weight   : 400;
	line-height   : 14px;
	opacity       : 0.7;
	padding       : 8px 16px;

	@media screen and (min-width : $break-table) {
		font-size   : 12px;
		line-height : 14px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 22px;
	}
}

.project-main__images {
	display               : grid;
	grid-gap              : 14px;
	grid-template-columns : repeat(6, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 28px;
	}

}

.project-main__img {
	grid-column : 6 span;

	img {
		border-radius : 8px;
	}
}
