.section-blog {
	background-color : #E9EADD;
	padding          : 40px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 64px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 160px 0 160px 0;
	}
}

.section-blog--secondary {
	background-color : #E9EADD;
}

.section-blog__wrapper {
	padding-bottom : 70px;
	position       : relative;

	@media screen and (min-width : $break-table) {
		padding-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.b-btn--outline-gray {
		bottom   : 0;
		left     : 0;
		position : absolute;

		@media screen and (min-width : $break-table) {
			position : unset;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.section-blog__head {
	align-items     : flex-start;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 35px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}

.blog-item {

}

.blog-item__img {
	border        : 1px solid #BFBCB4;
	border-radius : 8px;
	margin-bottom : 12px;
	padding       : 8px 8px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 32px;
		padding       : 12px 12px;
	}

	img {
		border-radius : 8px;
		height        : 100%;
		object-fit    : cover;
		width         : 100%;
	}
}

.blog-item__text {
	color          : $dark;
	font-size      : 16px;
	font-weight    : 400;
	line-height    : 20px;
	margin-bottom  : 12px;
	max-width      : 218px;
	text-transform : capitalize;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size     : 24px;
		line-height   : 32px;
		margin-bottom : 32px;
		max-width     : 266px;
	}
}

.blog-item__link {
	color                : $dark;
	font-size            : 14px;
	font-weight          : 400;
	line-height          : 24px;
	text-decoration-line : underline;
	text-transform       : uppercase;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.swiper--blog {
	margin : 0 -8px;
	@media screen and (min-width : $break-table) {
		margin : 0 -12px;
	}

	.swiper-slide {
		margin : 0 8px;
		width  : 65%;

		@media screen and (min-width : $break-table) {
			margin : 0 12px;
			width  : 31.5%;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}
