.project-hero {
	background-size : cover;
	padding         : 98px 0 41px 0;

	@media screen and (min-width : $break-table) {
		padding : 90px 0 36px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 240px 0;
	}
}

.project-hero--page {
	padding : 100px 0 100px 0;

	@media screen and (min-width : $break-table) {
		padding : 171px 0 109px 0;
	}

	@media screen and (min-width : $break-desktop) {
		display         : flex;
		flex-direction  : column;
		height          : 100vh;
		justify-content : center;
		padding         : 240px 0;
	}
}

.project-hero__title {
	color          : $white;
	font-family    : 'Bigilla';
	font-size      : 36px;
	font-style     : normal;
	font-weight    : 700;
	line-height    : 42px;
	text-align     : center;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 60px;
		line-height : 60px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 128px;
		line-height : 129px;
	}
}
