.find {
	padding : 40px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 64px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 160px 0 160px 0;
	}
}

.find__title {
	margin-bottom : 24px;
	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}

.find__wrap {

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
	}
	@media screen and (min-width : $break-desktop) {

	}
}

.find-map {
	border        : 1px solid #BFBCB4;
	border-radius : 8px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		max-width     : 312px;
	}
	@media screen and (min-width : $break-desktop) {
		max-width : none;
		padding   : 10px 28px 28px 28px;
	}
}

.find-map__img {
	height   : 248px;
	overflow : hidden;

	@media screen and (min-width : $break-table) {
		height : 324px;
	}
	@media screen and (min-width : $break-desktop) {
		height : auto;
	}

	img {
		height    : 478px;
		left      : -100px;
		max-width : none;
		position  : relative;
		top       : -33px;
		width     : 715px;

		@media screen and (min-width : $break-table) {
			height : 398px;
			left   : -148px;
			top    : -22px;
		}
		@media screen and (min-width : $break-desktop) {
			height    : auto;
			left      : 0;
			max-width : 100%;
			top       : 0;
		}
	}
}

.find-map__bottom {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	padding         : 8px 12px 12px 12px;
	position        : relative;
	z-index         : 2;
	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-top : -60px;
		padding    : 0;
	}
}

.find-map__name {
	background-color : #D6D8C1;
	border-radius    : 4px;
	color            : $dark;
	font-size        : 14px;
	font-weight      : 400;
	line-height      : 20px;
	padding          : 8px 16px;
	text-transform   : capitalize;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 30px;
		padding     : 10px 22px;
	}
}

.find-map__link {
	color                : $dark;
	font-size            : 14px;
	font-weight          : 400;
	line-height          : 20px;
	text-decoration-line : underline;
	text-transform       : uppercase;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		font-weight : 400;
		line-height : 24px;
	}
}

.find__subtitle {
	color          : $dark;
	font-size      : 24px;
	font-weight    : 400;
	line-height    : 32px;
	margin-bottom  : 16px;
	text-transform : capitalize;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		font-size     : 32px;
		line-height   : 48px;
		margin-bottom : 24px;
	}
}

.find__right {
	.b-btn--outline-gray {
		padding : 12px 24px;
	}

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		min-width : 400px;

		.b-btn--outline-gray {
			padding : 16px 32px;
		}
	}
}

.find__list {
	display       : flex;
	gap           : 45px;
	margin-bottom : 24px;
	width         : 100%;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-bottom : 36px;
	}

	ul {
		margin  : 0;
		padding : 0;
		width   : 50%;

		li {
			color        : $dark;
			font-size    : 14px;
			line-height  : 24px;
			padding-left : 15px;
			position     : relative;

			@media screen and (min-width : $break-table) {

			}
			@media screen and (min-width : $break-desktop) {
				font-size   : 16px;
				line-height : 24px;
			}


			&:before {
				background-color : $dark;
				border-radius    : 100%;
				content          : "";
				height           : 4px;
				left             : 0;
				position         : absolute;
				top              : 50%;
				transform        : translateY(-50%);
				width            : 4px;
			}
		}
	}
}
