.pagination {
	display         : flex;
	justify-content : center;
}

.pagination__list {
	align-items : center;
	display     : flex;
}

.pagination__item {

}

.pagination__link {
	align-items      : center;
	background-color : #F2EFE4;
	border-radius    : 4px;
	color            : $dark;
	display          : flex;
	font-size        : 14px;
	font-weight      : 400;
	height           : 32px;
	justify-content  : center;
	line-height      : 20px;
	transition       : background-color .3s ease;
	width            : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		height      : 48px;
		line-height : 24px;
		width       : 48px;
	}

	&:hover {
		background-color : #D6D8C1
	}
}

.pagination__arrow {
	display : flex;
	height  : 32px;
	width   : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 48px;
		width  : 48px;
	}

	svg {
		circle {
			fill       : transparent;
			transition : fill .3s ease;
		}

		path {
			position : relative;
			stroke   : #1C1C1C;
			z-index  : 2;
		}
	}

	&:hover {
		svg {
			object-fit : cover;

			circle {
				fill       : #D6D8C1;
				transition : fill .3s ease;
			}

			path {
				stroke : #1C1C1C;
			}
		}
	}
}

.pagination__item--first {
	margin-right : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-right : 32px;
	}
}

.pagination__item--last {
	margin-left : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 32px;
	}
}
