.about {
	padding : 120px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 120px 0 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 132px 0 80px 0;
	}
}

.about-top {
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 32px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap      : 54px;
		margin-bottom : 120px;
	}
}

.about-text {
	color       : #1C1C1C;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	p {
		&:not(:last-child) {
			margin-bottom : 24px;
		}
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}

}

.about-top__title {
	font-size   : 36px;
	grid-column : 4 span;
	line-height : 42px;

	@media screen and (min-width : $break-table) {
		grid-column : 1/5;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 64px;
		grid-column : 1/5;
		line-height : 64px;
	}
}

.about-top__text {
	&:nth-of-type(1) {
		grid-column : 4 span;

		@media screen and (min-width : $break-table) {
			grid-column : 8/13;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 7/10;
		}
	}

	&:nth-of-type(2) {
		grid-column : 4 span;

		@media screen and (min-width : $break-table) {
			grid-column : 8/13;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 10/13;
		}

	}
}

.about-middle {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 32px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 115px;
	}
}

.about-middle__icon {
	display : none;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		display     : block;
		grid-column : 1/1;
		grid-row    : 1/1;
	}
}

.about-middle__img {
	grid-column   : 4 span;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		grid-column   : 5/13;
		grid-row      : 1/5;
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.about-middle__text {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 1/4;
		grid-row    : 1/2;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/3;
		grid-row    : 4/4;
	}
}

.about-bottom {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
	}
}

.about-bottom__img {
	grid-column   : 4 span;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		grid-column   : 1/9;
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
	}
}

.about-bottom__text {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 10/13;
	}

	@media screen and (min-width : $break-desktop) {
	}
}

.about-img {
	img {
		border-radius : 8px;
		height        : 100%;
	}
}
