.feedback {
	padding : 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 32px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 160px 0 80px 0;
	}
}

.feedback__wrapper {
	background-color : #E9EADD;
	margin           : 0 -14px;
	overflow         : hidden;
	padding          : 0 0 40px 0;

	@media screen and (min-width : $break-table) {
		border-radius : 8px;
		margin        : 0 -32px;
		padding       : 32px 32px 32px 0;

	}
	@media screen and (min-width : $break-desktop) {
		margin  : 0;
		padding : 80px 92px 80px 0;
	}
}

.feedback__grid {
	display               : grid;
	grid-template-columns : repeat(4, 1fr);


	@media screen and (min-width : $break-table) {
		align-items           : center;
		grid-template-columns : repeat(12, 1fr);
	}
	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 78px;
	}
}

.feedback__img {
	grid-column   : 4 span;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		grid-column   : 1/7;
		grid-row      : 1/4;
		left          : -55px;
		margin-bottom : 0;
		position      : relative;

		img {


			@media screen and (min-width : $break-table) {
				height : 418px;
				width  : 312px;
			}
			@media screen and (min-width : $break-desktop) {
				height : auto;
				width  : auto;
			}
		}
	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 1/5;
		grid-row    : auto;
		left        : 0;
	}
}

.feedback__wrap {
	grid-column   : 4 span;
	margin-bottom : 32px;
	padding       : 0 14px;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 1/2;
		padding     : 0;
	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 5/9;
		grid-row    : auto;
	}
}

.feedback__form {
	grid-column : 4 span;
	padding     : 0 14px;
	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 2/4;
		padding     : 0;
	}
	@media screen and (min-width : $break-desktop) {
		grid-column : 9/13;
		grid-row    : auto;
	}
}

.feedback__title {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {

	}
}

.feedback__text {
	color       : $dark;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;


	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		max-width : 211px;
	}
}

.form--feedback {
	align-items           : flex-start;
	display               : grid;
	grid-gap              : 16px;
	grid-template-columns : 1fr;

	@media screen and (min-width : $break-table) {
		grid-gap : 10px;
	}
	@media screen and (min-width : $break-desktop) {
		grid-gap : 10px;
	}

	.b-btn--black {
		max-width : 89px;

		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			max-width : 176px;
		}
	}
}
