.services {
	background-color : #E9EADD;
	padding          : 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 160px 0;
	}
}

.services__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 64px;
	}
}


.services-item {

	width : 100%;

	@media screen and (min-width : $break-table) {
		border-bottom : 1px solid #BFBCB4;
	}
}

.services__wrap {
	counter-reset : count;
	margin        : 0 -14px;
	@media screen and (min-width : $break-table) {
		margin : 0;
	}
}

.services-item {

}

.services-item__control {
	align-items      : center;
	background-color : transparent;
	border           : none;
	display          : none;
	justify-content  : space-between;
	margin           : 48px 0 0 0;
	padding-left     : 100px;
	position         : relative;
	transition       : padding .3s ease;
	width            : 100%;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		display : flex;
	}

	&:before {
		color             : rgba(0, 0, 0, 0.27);
		content           : "0" counter(count);
		counter-increment : count;
		font-size         : 32px;
		font-weight       : 400;
		left              : 0;
		line-height       : 48px;
		position          : absolute;
		top               : 50%;
		transform         : translateY(-50%);
		z-index           : 1;
	}
}

.services-item__title {
	color          : $dark;
	font-size      : 32px;
	font-weight    : 400;
	line-height    : 48px;
	max-width      : 450px;
	text-align     : start;
	text-transform : capitalize;
}

.services-item__content {
	display        : flex;
	flex-direction : column-reverse;
	max-height     : none;
	opacity        : 1;
	overflow       : visible;
	transition     : all 0.3s ease-out;


	@media screen and (min-width : $break-table) {
		flex-direction : row-reverse;
		padding        : 32px 0;
	}
	@media screen and (min-width : $break-desktop) {
		box-sizing      : content-box;
		flex-direction  : row;
		justify-content : space-between;
		max-height      : 0;
		max-width       : 1000px;
		opacity         : 0;
		overflow        : hidden;
		padding         : 48px 0 0 100px;
		will-change     : max-height;
	}
}

.services-item__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		font-weight : 400;
		line-height : 24px;
		max-width   : 363px;
	}
}

.services-item__image {
	border        : 1px solid #BFBCB4;
	border-radius : 8px;
	flex-shrink   : 0;
	margin-bottom : 16px;
	padding       : 10px 10px;

	@media screen and (min-width : $break-table) {
		height        : 128px;
		margin-bottom : 0;
		padding       : 8px 8px;
		width         : 128px;
	}
	@media screen and (min-width : $break-desktop) {
		height        : 247px;
		margin-bottom : -106px;
		padding       : 16px 16px;
		width         : 247px;
	}

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.services-item__wrap {
	width : 100%;
	@media screen and (min-width : $break-table) {
		padding-left : 24px;
	}
	@media screen and (min-width : $break-desktop) {
		padding-left : 0;
	}
}

.services-item__top {
	margin-bottom : 12px;
	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.services-item__head {
	display         : flex;
	justify-content : space-between;

	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.services-item__name {
	color       : #1C1C1C;
	font-size   : 24px;
	font-weight : 400;
	line-height : 28px;
}

.services-item__count {
	color       : rgba(0, 0, 0, 0.27);
	font-size   : 16px;
	font-weight : 400;
	line-height : 20px;
}

.services-item__icon--mob {
	height : 32px;
	width  : 32px;
}

.services-item.open .services-item__content {
	opacity        : 1;
	overflow       : visible;
	padding-bottom : 48px;
	padding-top    : 24px;
}

.services-item.open .services-item__control {
	transition : padding .3s ease;

	&:before {
		top       : 50%;
		transform : translateY(-50%);
	}
}

.swiper--services {
	@media screen and (min-width : $break-table) {
		overflow : visible;
	}

	.swiper-wrapper {
		@media screen and (min-width : $break-table) {
			flex-direction : column;

		}
	}

	.swiper-slide {
		margin : 0 8px;
		width  : 75%;

		&:first-child {
			margin-left : 14px;
			@media screen and (min-width : $break-table) {
				margin : 0;

			}
		}

		&:last-child {
			margin-right : 14px;
			@media screen and (min-width : $break-table) {
				margin : 0;

			}
		}

		@media screen and (min-width : $break-table) {
			margin : 0;
			width  : 100%;
		}
		@media screen and (min-width : $break-desktop) {

		}
	}
}
