.header {
	left     : 0;
	padding  : 0 14px;
	position : absolute;
	top      : 0;
	width    : 100%;
	z-index  : 1;
}

.header--secondary {
	.header__container {
		border-bottom : 1px solid rgba(28, 28, 28, 0.4);
	}

	.nav__link {
		color : $dark;
	}

	.header__contacts {
		a {
			color : $dark;
		}

		span {
			background-color : $dark;
		}
	}

	.header__location {
		color : $dark;
	}

	.b-btn--outline-white {
		border : 2px solid #BFBCB4;
		color  : $dark;

		&:hover {
			border : 2px solid transparent;
		}
	}

	.header__burger {
		svg {
			path {
				fill : $dark;
			}
		}
	}

	.header__logo {
		svg {
			path {
				fill : $dark;
			}
		}
	}
}

.mobile-logo {
	height : 40px;
	width  : 40px;

	svg {
		path {
			fill : $dark;
		}
	}
}

.header__container {
	border-bottom : 1px solid rgba(255, 255, 255, 0.4);
	margin        : 0 auto;
	max-width     : 1792px;
	padding       : 11px 0;

	@media screen and (min-width : $break-table) {
		padding : 20px 0;
	}
}

.header__panel {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.header__left {
	align-items : center;
	display     : flex;
}

.header__logo {
	width: 40px;
	height: 40px;
	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	left: 0;
	z-index: 1;

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		margin-left: -132px;
		position: unset;
		transform: unset;
		width: 60px;
		height: 60px;
	}

	@media screen and (min-width : 1560px) {
		margin-left: -274px;
	}
}

.nav {
	border-bottom : 1px solid #1C1C1C;
	padding       : 48px 0;
	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		border-bottom : none;
		padding       : 0;
	}
}

.nav__list {

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		align-items : center;
		display     : flex;
	}
}

.nav__item {
	& + & {
		margin-top : 16px;
	}

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		& + & {
			margin-left: 35px;
			margin-top  : 0;
		}
	}

	@media screen and (min-width : 1336px) {
		& + & {

		}


	}


	@media screen and (min-width : 1560px) {
		& + & {
			margin-left: 64px;
		}


	}
}

.nav__link {
	color          : $dark;
	font-size      : 16px;
	font-weight    : 400;
	line-height    : 24px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		color : $white;
	}
}

.header__wrap {
	align-items : center;
	display     : flex;

	.b-btn--outline-white {
		display : none;
		@media screen and (min-width : $break-table) {

		}
		@media screen and (min-width : $break-desktop) {
			display : flex;
		}
	}
}

.header__contacts {

	@media screen and (min-width : $break-table) {
		align-items : center;
		display     : flex;
		gap         : 10px;
		position    : relative;
	}
	@media screen and (min-width : $break-desktop) {
		display      : block;
		margin-right : 40px;
	}

	a {
		color       : $white;
		font-size   : 10px;
		font-style  : normal;
		font-weight : 400;
		line-height : 24px;

		@media screen and (min-width : $break-table) {
			font-size   : 12px;
			line-height : 14px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 14px;
		}
	}

	span {
		background-color : $white;
		border-radius    : 100%;
		display          : none;
		height           : 2px;
		left             : 136px;
		width            : 2px;

		@media screen and (min-width : $break-table) {
			display : block;
		}

		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}
}

.header__location {
	color          : $white;
	font-size      : 10px;
	font-weight    : 400;
	line-height    : 14px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 12px;
		line-height : 14px;
	}

}

.header__overlay {
	background : rgba(0, 0, 0, .7);
	bottom     : 0;
	left       : 0;
	opacity    : 0;
	position   : fixed;
	right      : 0;
	top        : 0;
	transition : opacity .6s ease, visibility .6s ease;
	visibility : hidden;
	z-index    : 100;

	@media screen and (min-width : $break-desktop) {
		background : transparent;
		opacity    : 1;
		position   : unset;
		visibility : visible;
	}

	&.active {
		opacity    : 1;
		visibility : visible;
	}
}

.header__menu {
	background-color : #E9EADD;
	height           : 100svh;
	position         : absolute;
	right            : -100%;
	top              : 0;
	transition       : left .3s ease-in;
	width            : 100%;
	z-index          : 100;

	@media screen and (min-width : $break-table) {
		max-width : 320px;
	}

	@media screen and (min-width : $break-desktop) {
		background-color : transparent;
		height           : auto;
		max-width        : none;
		padding          : 0;
		position         : unset;
	}

	&.active {
		right      : 0;
		transition : left .3s ease-in;
	}
}

.header__burger {
	background-color : transparent;
	border           : none;
	margin-left      : 24px;

	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.header__close {
	background-color : transparent;
	border           : none;
}

.header__top {
	align-items     : center;
	border-bottom   : 1px solid #1C1C1C;
	display         : flex;
	justify-content : space-between;
	padding-bottom  : 20.5px;

	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.header__wrapper {
	display        : flex;
	flex-direction : column;
	height         : 100%;
	overflow-y     : auto;
	padding        : 16px 16px 40px 16px;
	@media screen and (min-width : $break-desktop) {
		overflow-y : visible;
		padding    : 0;
	}
}

.header__contact {
	padding-top : 48px;
	@media screen and (min-width : $break-desktop) {
		display : none;
	}
}

.contacts--header {
	flex-direction : column;
}
