.highlights {
	padding : 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0;
	}
}

.highlights__title {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.highlights-item {
	padding : 0 16px;

	@media screen and (min-width : $break-desktop) {
		padding : 0;
	}
}

.highlights-item__top {
	align-items   : center;
	display       : flex;
	margin-bottom : 16px;
}

.highlights__icon {
	flex-shrink : 0;
}


.highlights__name {
	color       : $dark;
	font-size   : 20px;
	font-weight : 400;
	line-height : 26px;
	margin-left : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 32px;
		margin-left : 16px;
	}
}

.highlights-item__list {
	li {
		color        : $dark;
		font-size    : 14px;
		font-weight  : 400;
		line-height  : 20px;
		padding-left : 20px;
		position     : relative;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size    : 16px;
			font-weight  : 400;
			line-height  : 24px;
			padding-left : 20px;
		}


		&:before {
			background-color : $dark;
			border-radius    : 100%;
			content          : "";
			height           : 4px;
			left             : 8px;
			position         : absolute;
			top              : 10px;
			width            : 4px;
			z-index          : 1;
		}
	}
}

.swiper--highlights {
	margin : 0 -16px;

	@media screen and (min-width : $break-desktop) {
		margin : 0;
	}

	.swiper-wrapper {
		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			display               : grid;
			grid-gap              : 76px 24px;
			grid-template-columns : repeat(12, 1fr);
		}
	}

	.swiper-slide {
		align-items    : flex-start;
		display        : flex;
		flex-direction : column;
		height         : auto;
		margin         : 0 10px;
		width          : 77%;

		&:not(:first-child) {
			border-left : 1px solid #BFBCB4;
			@media screen and (min-width : $break-desktop) {
				padding-left : 24px;
			}
		}

		@media screen and (min-width : $break-table) {
			width : 67%;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 4 span;
			margin      : 0;
			width       : 100%;

			&:nth-child(4) {
				border-left  : none;
				padding-left : 0;
			}
		}
	}
}

.highlights-slider {
	padding : 40px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 32px 0 0 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0 0 0;
	}
}

.swiper--highlight {

	.swiper-slide {
		img {
			border-radius : 8px;
			object-fit    : cover;
			width         : 100%;
		}
	}

	.swiper-nav {
		justify-content : center;
		margin-top      : 12px;

		@media screen and (min-width : $break-desktop) {
			margin-top : 22px;
		}
	}
}
