
.overlay {
	//opacity: 0;
	background : rgba(0, 0, 0, 0.85);
	bottom     : 0;
	display    : none;
	left       : 0;
	position   : fixed;
	right      : 0;
	//z-index: -1;
	top        : 0;
	transition : opacity 0.5s ease;
	z-index    : 101;

	&.active {
		display : flex !important;
		/*  z-index: 9;
		opacity: 1;
		transition: opacity 0.5s ease; */
	}
}

.popup {
	display    : none;
	left       : 50%;
	max-width  : 320px;
	padding    : 0 15px;
	//z-index: -3;
	position   : fixed;
	top        : 50%;
	transform  : translate(-50%, -45%);
	transition : opacity 0.5s ease;
	//opacity: 0;
	width      : 100%;
	z-index    : 103;


	&.active {
		display    : block;
		//opacity: 1;
		transition : opacity 0.5s ease;
		//transform: translate(-50%, -45%);
		z-index    : 103;
	}

	&__wrap {
		align-items      : center;
		background-color : #F2EFE4;
		border-radius    : 8px;
		display          : flex;
		flex-direction   : column;
		justify-content  : center;
		min-height       : 220px;
		overflow         : hidden;
		padding          : 30px;
		position         : relative;
	}

	&__close {
		background-color : transparent;
		border           : none;
		position         : absolute;
		right            : 28px;
		top              : 16px;
		z-index          : 999;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.popup--process {
	padding : 0 15px;

	@media screen and (min-width : $break-table) {
		max-width : 582px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.popup__wrap {
		padding : 0;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}


.popup-process__wrap {
	padding : 24px 12px;

	@media screen and (min-width : $break-table) {
		padding : 64px 64px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.popup-process__title {
	color          : $dark;
	font-family    : 'Bigilla';
	font-size      : 36px;
	font-style     : normal;
	font-weight    : 700;
	line-height    : 42px;
	margin-bottom  : 16px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.popup-process__text {
	color       : $dark;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;
}

.popup--founder {


	@media screen and (min-width : $break-table) {
		max-width : 730px;

	}

	@media screen and (min-width : $break-desktop) {

	}

	.popup__wrap {
		padding : 24px 16px;

		@media screen and (min-width : $break-table) {
			padding : 48px 48px;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 64px 64px;
		}
	}
}

.popup-founder__title {
	color          : $dark;
	font-family    : 'Bigilla';
	font-size      : 36px;
	font-style     : normal;
	font-weight    : 700;
	line-height    : 42px;
	margin-bottom  : 16px;
	max-width      : 224px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		max-width : none;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.popup-founder__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;
	max-height  : 511px;
	overflow-y  : auto;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	p {
		&:not(:last-child) {
			margin-bottom : 15px;
			@media screen and (min-width : $break-table) {
				margin-bottom : 25px;
			}
		}
	}
}

.popup--contact {


	@media screen and (min-width : $break-table) {
		max-width : 712px;

	}

	@media screen and (min-width : $break-desktop) {
		max-width : 885px;
	}

	.popup__wrap {
		padding : 32px 16px;

		@media screen and (min-width : $break-table) {
			padding : 48px 0;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 64px 0;
		}
	}
}

.popup-contact {
	align-items           : center;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(8, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.popup-contact__img {
	display     : none;
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		display : block;
	}

	img {
		height   : 456px;
		left     : -50px;
		position : relative;
		width    : 516px;
	}
}

.popup-contact__form {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		padding-right : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-right : 56px;
	}
}

.popup-contact__wrap {
	margin-bottom : 28px;


}

.popup-contact__title {
	color          : $dark;
	font-family    : 'Bigilla';
	font-size      : 36px;
	font-style     : normal;
	font-weight    : 700;
	line-height    : 42px;
	margin-bottom  : 16px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size     : 36px;
		line-height   : 42px;
		margin-bottom : 8px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 48px;
		line-height : 56px;
	}
}

.popup-contact__text {
	color       : $dark;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		max-width : 216px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
