@font-face {
	font-family  : 'AeonikProTRIAL';
	src          : url('../fonts/AeonikProTRIAL-Regular.woff2') format('woff2'),
	url('../fonts/AeonikProTRIAL-Regular.woff') format('woff'),
	url('../fonts/AeonikProTRIAL-Regular.ttf') format('truetype');
	font-weight  : 400;
	font-style   : normal;
	font-display : swap;
}

@font-face {
	font-family  : 'Bigilla';
	src          : url('../fonts/BigillaBold.woff2') format('woff2'),
	url('../fonts/BigillaBold.woff') format('woff'),
	url('../fonts/BigillaBold.ttf') format('truetype');
	font-weight  : 700;
	font-style   : normal;
	font-display : swap;
}
