.founder {
	padding : 40px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 64px 0 144px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 160px 0 160px 0;
	}
}

.founder-item__grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(3, 1fr);
	@media screen and (min-width : $break-table) {
		align-items           : flex-start;
		grid-column-gap       : 20px;
		grid-row-gap          : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap       : 26px;
		grid-row-gap          : 0;
		grid-template-columns : repeat(12, 1fr);
	}

	.b-btn--outline-gray {
		grid-row : 6/6;
		padding  : 12px 24px;

		@media screen and (min-width : $break-table) {
			grid-column : 9/12;
			grid-row    : 3/4;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 1/3;
			grid-row    : 8/8;
			padding     : 16px 32px;
		}
	}
}

.founder-item__title {
	color          : $dark;
	font-size      : 36px;
	font-weight    : 700;
	grid-column    : 1/4;
	grid-row       : 1/1;
	line-height    : 42px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		grid-column   : 1/13;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 48px;
		line-height   : 56px;
		margin-bottom : 24px;
	}
}

.founder-item__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	grid-column : 1/4;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		grid-column   : 1/5;
		grid-row      : 2/4;
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column   : 1/4;
		margin-bottom : 32px;
	}
}

.founder-item__link {
	align-items    : center;
	color          : $dark;
	display        : flex;
	font-size      : 14px;
	font-weight    : 400;
	grid-column    : 1/3;
	line-height    : 22px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		grid-column : 1/5;
		grid-row    : 3/3;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		grid-column : 1/4;
		grid-row    : 4/4;
		line-height : 24px;
	}

	img {
		margin-left : 8px;
	}
}

.founder-item__img {
	border        : 1px solid #BFBCB4;
	border-radius : 8px;
	grid-column   : 1/4;
	padding       : 12px 12px;

	@media screen and (min-width : $break-table) {
		grid-column : 5/9;
		grid-row    : 2/4;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4/9;
		grid-row    : 1/9;
	}

	img {
		object-fit : cover;
		width      : 100%;
	}
}


.founder-item__info {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 9/13;
		grid-row    : 2/4;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 9/13;
		grid-row    : 1/5;
	}
}

.founder-item__about {
	color          : rgba(51, 51, 51, 0.6);
	font-size      : 14px;
	font-weight    : 400;
	line-height    : 20px;
	text-transform : capitalize;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 16px;
	}
}

.founder-item__name {
	color          : $dark;
	font-family    : "Bigilla";
	font-size      : 28px;
	font-weight    : 700;
	line-height    : 34px;
	margin-bottom  : 24px;
	text-transform : capitalize;

	@media screen and (min-width : $break-table) {
		font-size   : 24px;
		line-height : 34px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 48px;
		line-height   : 48px;
		margin-bottom : 40px;
	}
}

.contacts {
	&__item {
		align-items : center;
		display     : flex;

		&:not(:last-child) {
			margin-bottom : 16px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 24px;
			}
		}
	}

	&__wrap {
		margin-left : 16px;
	}

	&__name {
		color          : $dark;
		font-size      : 14px;
		font-weight    : 400;
		line-height    : 20px;
		text-transform : capitalize;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 24px;
		}
	}

	&__link {
		color       : $dark;
		font-size   : 12px;
		font-weight : 400;
		line-height : 18px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 20px;
			line-height : 26px;
		}
	}

	&__icon {
		flex-shrink : 0;
		height      : 32px;
		width       : 32px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			height : 48px;
			width  : 48px;
		}

	}
}
