.hero {
	background-repeat : no-repeat;
	background-size   : cover;
	padding           : 151px 0 102px 0;

	@media screen and (min-width : $break-table) {
		padding : 91px 0 232px 0;
	}
	@media screen and (min-width : $break-desktop) {
		padding : 188px 0 213px 0;
	}
}

.hero__wrapper {
	align-items    : center;
	display        : flex;
	flex-direction : column;
}

.hero__title {
	color          : $white;
	font-family    : 'Bigilla';
	font-size      : 36px;
	font-style     : normal;
	font-weight    : 700;
	line-height    : 42px;
	margin-bottom  : 4px;
	text-align     : center;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 60px;
		line-height : 60px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size     : 154px;
		line-height   : 155px;
		margin-bottom : 7px;
	}
}

.hero__text {
	color         : $white;
	font-size     : 16px;
	line-height   : 20px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 16px;
	}
	@media screen and (min-width : $break-desktop) {
		font-size     : 24px;
		line-height   : 32px;
		margin-bottom : 31px;
	}
}
